.guessingApp {
  text-align: center;
}

.guessForm {
  background-color: #fff;
  margin: 50px auto;
  padding: 10%;
  border-radius: 25px;
}

header h1 {
  font-size: 3em !important;
}

.header {
    border-radius: 10%;
}

.newGame {
    position: absolute;
    top: 10px;
    right: 10px;
}

/* Mobile Portrait and Landscape */
@media screen
  and (max-device-width: 640px) {
      .guessForm {
          padding: 5% 10% 20%;
      }
}



